<template>
  <Layout id="TalReportList" class="gary">
    <Form :label-width="80" inline>
      <FormItem label="报表名称：">
        <Input v-model="name"></Input>
      </FormItem>
      <FormItem>
        <Button :loading="loading" @click="search" type="primary">查询</Button>
      </FormItem>
    </Form>
    <Table :columns="cols" :data="dataList">
      <template slot-scope="{ row }" slot="opt">
        <a @click="getUrl(row.reportId)">查询</a>
      </template>
    </Table>
  </Layout>
</template>

<script>
import { reportList, getQuickUrl } from '@/api/report'
export default {
  name: 'reportList',
  data () {
    return {
      name: '',
      cols: [
        {
          title: '报表名称',
          key: 'name',
          align: 'center'
        },
        {
          title: '创建时间',
          key: 'createDate',
          align: 'center'
        },
        {
          title: '创建人',
          key: 'applyEmpName',
          align: 'center'
        },
        {
          title: '报表标签',
          key: 'label',
          align: 'center'
        },
        {
          title: '报表描述',
          key: 'remark',
          align: 'center'
        },
        {
          title: '操作',
          slot: 'opt',
          align: 'center'
        }
      ],
      dataList: [],
      loading: ''
    }
  },
  methods: {
    search () {
      this.loading = true
      reportList({
        name: this.name
      }).then(data => {
        this.dataList = data
      }).catch(err => {
        this.$Message.error({
          content: '查询报表列表错误：' + err.message,
          duration: 5
        })
      }).finally(() => {
        this.loading = false
      })
    },
    getUrl (reportId) {
      getQuickUrl({
        reportId
      }).then(data => {
        setTimeout(() => {
          window.open(data)
        }, 500)
      }).catch(err => {
        this.$Message.error({
          content: '获取图表url错误：' + err.message,
          duration: 5
        })
      })
    }
  },
  mounted () {
    this.search()
  }
}
</script>

<style scoped lang="less">
  #TalReportList {
    overflow-x: hidden;
    padding: 10px;
  }

</style>
