import axios from '@/libs/api.request'

/**
 * 实时报表查询列表
 * @param params
 * @returns {*}
 */
export const reportList = params => {
  return axios.request({
    url: '/quickReport/list',
    params,
    method: 'get'
  })
}
/**
 * 获取图表url
 * @param params
 * @returns {*}
 */
export const getQuickUrl = params => {
  return axios.request({
    url: '/quickReport/getQuickUrl',
    params,
    method: 'get'
  })
}
